.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  min-height: 100vh;
  padding: 50px;
}

.converter-container {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  display: inline-block;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.converter-inputs input, .converter-inputs select {
  padding: 10px;
  margin: 5px;
  font-size: 16px;
}

.converter-inputs button {
  padding: 10px 15px;
  font-size: 16px;
  margin: 5px;
}

.convert-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
}

.result {
  margin-top: 20px;
  color: #333;
}

